@import "colors";

.home {
  .btnAddList {
    position: fixed;
    z-index: 999;
    left: -150px;
    top: 20%;
    width: 200px;
    transition: all ease-out 450ms;
    &:hover {
      left: -10px;
      transition: all ease-in 190ms;
      p {
        opacity: 1;
        transition: all ease-in 100ms;
        transition-delay: 150ms; } }
    p {
      display: inline-block;
      margin: 5px 20px;
      opacity: 0;
      transition: all ease-out 200ms; } }
  .homeListTile {
    position: relative;
    padding: 30px 30px 45px 30px;
    margin: 20px;
    border: 1px solid #eee;
    background-color: #ffffff;
    hr {
      height: 2px;
      background-color: $lightBlue; }
    .btnDelete {
      position: absolute;
      top: 5px;
      right: 5px; }
    .btnOpenList {
      margin-top: 30px; } }
  .btnPreviewCategory {
    position: absolute;
    width: 100%;
    bottom: 20px;
    right: 0;
    left: 0;
    margin: 0 auto;
    background-color: transparent;
    color: skyblue; }

  .categoryImg {
    height: 30px; }

  .productsList {
    .product {
      padding: 30px;
      border: 1px solid #eee;
      border-radius: 15px;
      text-align: center;
      margin: 20px;
      position: relative;
      -webkit-box-shadow: 9px 10px 46px -15px rgba(200,200,200,1);
      -moz-box-shadow: 9px 10px 46px -15px rgba(200,200,200,1);
      box-shadow: 9px 10px 46px -15px rgba(200,200,200,1);
      transition: all ease-in-out 200ms;
      cursor: pointer;
      background-blend-mode: lighten;
      background-repeat: no-repeat !important;
      background-color: rgba(255,255,255,0.6) !important;
      background-position: top center !important;
      &:hover {
        transition: all ease-in-out 200ms;
        -webkit-box-shadow: unset;
        -moz-box-shadow: unset;
        box-shadow: unset; }
      .productImg {
        width: auto;
        height: 70px;
        max-width: 100%;
        display: block;
        margin: 10px auto; }
      .name {
        font-weight: bold;
        margin: 10px;
        color: #444444; }
      .nutrientsSwitch {
        margin: 8px auto;
        color: $darkGreen; }
      .nutrients {
        height: 0;
        border-top: 1px solid $lightBlue;
        padding-top: 5px;
        overflow: hidden;
        font-size: 12px;
        font-weight: bold;
        color: grey;
        text-align: left;
        transition: all ease-in-out 200ms;
        &_info {
          font-size: 11px;
          color: #ccc; } }
      .badge-times {
        padding: 7px 12px;
        position: absolute;
        top: 14px;
        left: 0;
        border-bottom-left-radius: 0;
        border-top-left-radius: 0; }
      .badge-category {
        background-color: white;
        width: 120px;
        border: 2px solid; } }
    [data-category="meat"] {
      .badge-category {
        border-color: #fda2a2 !important;
        color: #fda2a2 !important; } }
    [data-category="dairy"] {
      .badge-category {
        border-color: lightgrey !important;
        color: lightgrey !important; } }
    [data-category="bread"] {
      .badge-category {
        border-color: #b79569 !important;
        color: #b79569 !important; } }
    [data-category="vegetables"] {
      .badge-category {
        border-color: #90bc90 !important;
        color: #90bc90 !important; } }
    [data-category="fruits"] {
      .badge-category {
        border-color: #ffd9a4 !important;
        color: #ffd9a4 !important; } }
    [data-category="frozen-food"] {
      .badge-category {
        border-color: #aee5e4 !important;
        color: #aee5e4 !important; } }
    [data-category="sweets"] {
      .badge-category {
        border-color: #d9a7b8 !important;
        color: #d9a7b8 !important; } }
    [data-category="chemistry"] {
      .badge-category {
        border-color: #98d81d !important;
        color: #98d81d !important; } }
    [data-category="drinks"] {
      .badge-category {
        border-color: #ffebce !important;
        color: #ffebce !important; } } } }

